<template>
	<view class="my-order">
		<view class="tabs">
			<u-tabs 
				:list="list" 
				lineWidth="30" 
				lineColor="#FE542C" 
				:activeStyle="{
					color: '#333',
					transform: 'scale(1.05)'
				}" 
				:inactiveStyle="{
						color: '#606266',
						transform: 'scale(1)'
				}" 
				itemStyle="padding-bottom: 12rpx; height: 34px; width: 13%;"
				:current="current"
				@click="click"
			>
			</u-tabs>
		</view>
		<scroll-view 
			v-if="orderList.length>0"
			class="order-list" 
			scroll-y="true" 
			:show-scrollbar="false"
		>
			<view class="content" v-for="item in 8" :key="item">
				<view style="height: 18rpx;"></view>
				<view class="title">
					<view>
						我就要买apple产品自营店
					</view>
					<view style="color: #FE542C;">
						待支付
					</view>
				</view>
				<view class="info">
					<view class="">
						<u--image :showLoading="true" :src="src" width="211rpx" height="237rpx"></u--image>
					</view>
					<view style="padding-left: 46rpx;">
						<view style="line-height: 38rpx; font-weight: 600;">
							苹果（Apple)iphone XR国行
						</view>
						<view style="color: #999; font-size: 24rpx;line-height: 34rpx;">
							规格：深空灰 64G 全网通
						</view>
						<view style="font-size: 30rpx;line-height: 40rpx; font-weight: 600;">
							￥6299
						</view>
						<view style="display: flex;justify-content: flex-end;">
							<u-icon name="arrow-right" color="#979797"></u-icon>
						</view>
					</view>
				</view>
				<view class="cell">
					<view class="">
						<text>首付:¥1000</text>
						 <text style="margin: 0 18rpx;">每期应还:¥429</text> 
						 <text>期数:12</text>
					</view>
				</view>
				<view class="action">
					<view class="">
						<u-button text="取消订单" size="small"></u-button>
					</view>
					<view style="padding-left: 18rpx;">
						<u-button text="立即支付" type="error" size="small" plain></u-button>
					</view>
					<view style="padding-left: 18rpx;">
						<u-button @click="goTracking" text="订单跟踪" type="error" size="small" plain></u-button>
					</view>
				</view>
			</view>
			<view style="height: 30rpx;"></view>
		</scroll-view>
		<view class="order-list" v-else>
			<view 
				style="height: 100%;
				display: flex;
				align-items: center;
				justify-content: center;"
			>
				<u-empty :icon="emtpyImg" text="这里空空的哦~"></u-empty>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				list: [
					{ name: '全部' }, 
					{ name: '待付款' }, 
					{ name: '待签约' },
					{ name: '待发货' },
					{ name: '待收货' }
				],
				current: 0,
				src: 'https://cdn.uviewui.com/uview/album/1.jpg',
				orderList: [],
				emtpyImg: ''
			}
		},
		
		onLoad(option) {
			console.log(option.index);
			this.current = option.index;
			this.emtpyImg = require('../../static/empty.png')
		},
		
		methods: {
			// tab点击事件
			click(item) {
				this.current = item.index;
			},
			
			// 订单跟踪
			goTracking() {
				uni.navigateTo({
					url: '/subPackages/orderTracking/orderTracking'
				})
			}
		}
	}
</script>

<style scoped lang="scss">
	.my-order {
		color: #333;
		font-size: 28rpx;
		display: flex;
		flex-direction: column;
		height: 100vh;
	}
	.tabs {
		height: 80rpx;
		background-color: #fff;
	}
	.order-list {
		flex: 1;
		height: calc(100vh - 80rpx);
	}
	.content {
		margin-left: 18rpx;
		margin-right: 18rpx;
		border-radius: 12rpx;
		.title {
			height: 72rpx;
			line-height: 72rpx;;
			font-size: 25rpx;
			font-family: PingFangSC, PingFang SC;
			font-weight: 600;
			padding: 0 18rpx;
			display: flex; 
			justify-content: space-between; 
			align-items: center;
			background-color: #fff;
		}
		.info {
			display: flex;
			padding: 18rpx 40rpx;
			background: #FBFBFB;
		}
		.cell {
			height: 54rpx;
			line-height: 54rpx;
			background-color: #fff;
			color: #666;
			text-align: end;
			font-size: 24rpx;
			padding-right: 18rpx;
		}
	}
	.action {
		background-color: #fff;
		display: flex;
		justify-content: flex-end;
		height: 72rpx;
		align-items: center;
		padding-right: 18rpx;
	}
	
	::v-deep .u-button--error {
		border-color: #FE542C !important;
	}
	
	::v-deep .u-button--plain.u-button--error {
		color: #FE542C !important;
	}
</style>